@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@1,200&family=Nunito&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: nunito;
}

.navbar {
  background-color: #fff !important;
}

.navbar-brand {
  font-size: 1.9rem !important;
  color: #4d4dff !important;
}

.navbar a {
  font-size: 1.3rem;
  text-transform: capitalize;
  color: #ff33ff;
  transition: 0.5s ease;
}
.menu_active {
  font-weight: bold;
  border-bottom: 1px solid #3498db;
  transition: 0.5s ease;
}

.navbar a:hover {
  color: #3498db;
  transition: 0.5s ease;
}

.hb1 {
  color: #3498db;
}

.hb2 {
  color: #ff33ff;
  text-transform: uppercase;
}
.btn-get-started {
  text-decoration: none;
  font-size: 30px;
  color: #fff;
  background-color: #3498db;
  padding: 5px 25px;
  border-radius: 25px;

  transition: 0.7s ease;
}
.btn-get-started:hover {
  color: #ff33ff;
  background-color: transparent;

  border: 1px solid #ff33ff;
  transition: 0.5s ease;
}
#header {
  width: 100%;
  height: 80vh;
  margin-top: 40px;
}

#header h1 {
  margin: 0 0 10px 0;
  font-size: 45px;
  font-weight: 600;
  color: #111111;
}
#header h3 {
  color: #484848;
  font-size: 24px;
}
.header-img {
  text-align: right;
}

#header .animated {
  animation: cakim 3s ease-in-out infinite alternate-reverse both;
}
.tit {
  color: #ff33ff;
  font-weight: 800;
}
.tit1 {
  color: #3498db !important;
  font-weight: 800;
}
.car {
  height: 250px;
}
.picse {
  width: 100%;
  height: 100%;
}
.picc {
  height: 300px;
}

.caked {
  background: linear-gradient(#000, transparent);
  z-index: 2;
  justify-content: center;
}

.caked h5 {
  font-size: 40px;
  font-weight: 800;
  margin-bottom: 10px;
}

.caked p {
  font-size: 20px;
  font-weight: 400;
}
.btn-or {
  text-decoration: none;
  font-size: 30px;
  color: #fff;
  background-color: #3498db;
  padding: 5px 35px;
  border-radius: 25px;
  cursor: pointer;
  transition: 0.7s ease;
}
.btn-or:hover {
  color: #fff;
  font-weight: 900;
  background-color: transparent;
  padding: 5px 45px;

  border: 2px solid #ff33ff;
  transition: 0.5s ease;
}
.mcc {
  color: #3498db;
}
.ind {
  height: 300px;
  width: 100%;
}
.menubr {
  text-decoration: none;
}

@-webkit-keyframes cakim {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.9);
  }
}

@keyframes cakim {
  0% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}

@media (max-width: 591px) {
  .navbar-brand {
    font-size: 1.5rem !important;
  }
  .menu_active {
    font-weight: bold;
    border-bottom: none;
  }
  .picer {
    height: 200px;
  }
  .car {
    height: 430px;
  }
  .ck1 {
  }
  .btn-get-started {
    font-size: 20px;
    padding: 5px 25px;
    margin: 0 auto;

    transition: 0.7s ease;
  }
  .caked {
    background: linear-gradient(#000, transparent);
    z-index: 2;
    justify-content: center;
  }

  .caked h5 {
    font-size: 30px;
    font-weight: 800;
    margin-bottom: 10px;
  }

  .caked p {
    font-size: 18px;
    font-weight: 400;
  }
  .btn-or {
    text-decoration: none;
    font-size: 20px;
    color: #fff;
    background-color: #3498db;
    padding: 5px 20px;
    border-radius: 25px;

    transition: 0.7s ease;
  }
  .btn-or:hover {
    color: #ff33ff;
    background-color: #000;

    border: 1px solid #ff33ff;
    transition: 0.5s ease;
  }
}

